.custom-scroll::-webkit-scrollbar {
  width: 8px;
}

.custom-scroll::-webkit-scrollbar-thumb {
  border-radius: 50px;
  border: 1px solid #ccc;
  background-color: #fff;
}

.custom-scroll::-webkit-scrollbar-thumb:hover {
  border: 1px solid gray;
}

.app {
  width: 100%;
  height: 100vh;
  display: flex;
  background-color:#eeeeee;
  flex-direction: column;
}

.app_nav {
  padding: 30px;
  box-shadow: 0 1px 20px rgba(56, 40, 40, 0.05);
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  position: sticky;
  top: 0;
  background-color: #fff;
}

.app_boards_container {
  flex: 1;
  width: 100%;
  height: 100%;
  overflow-x: auto;
  padding-top: 20px;
}

.app_boards {
  height: 100%;
  width: fit-content;
  padding: 10px 30px;
  display: flex;
  gap: 30px;
}

.app_boards_last {
  flex-basis: 290px;
  min-width: 290px;
}

.app_boards_add-board {
  background-color: #fff;
  color: #000;
  border-radius: 10px;
  box-shadow: 1px 1px 0 1px rgba(0, 0, 0, 0.12);
  width: 100%;
  text-align: center;
}
.app_boards_add-board_edit {
  background-color: #fff;
  border-radius: 10px;
  padding: 10px;
}
.button {
  width: "12px";
  background-color: #fff;
  border-color:#ccc;
  padding: 10px 5px 10px 10px;
  flex-direction: column;
  border-width: 1px;
  border-radius: 5px
}

.drop-down {
  box-shadow: 0 4px 6px 0 rgba(0, 0, 0, 0.1);
  transition: 0.3s;
  width: 250px;
  background-color: #fff;
  flex-direction: column;
  border-radius: 10px;
  padding: 20px;
}

.drop-down:hover {
  box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.2);
}

.select {
  width: 20;
  background-color: #fff;
  border-color:#ccc;
  padding: 3px;
  margin: 1px;
  flex-direction: column;
  border-width: 1px;
  border-radius: 5px;
}